.foot-container{
    display: flex;
}
.exmoo{
    background: linear-gradient(to right, #ffffff, #3c78a0);
    -webkit-background-clip: text;
    color: transparent; /* Text is made transparent to show the gradient */
    padding-left: 50px;
    padding-right: 40px;
    
}
.headere{
    display: flex  ;
    width: 90%;
}
.left-f{
    width: 50%;
    background-color: #1a1a1a;
    padding: 20px;
}

.left-f .hr-line{
    width: 85%;
}
.footer-links{
    display: flex;
    gap: 50px;
    margin-top: 20px;
    padding-left: 70px;
}
.pp{
    display: flex;
    gap: 10px;
}
.subs{
    width: 70%;
    font-size: 1.5rem;
}
.subs h1{
    text-align: center;
}
.etext{
    height: 50px;
}
.esub{
    position: absolute;
   
   
}
.icons{
    background-color: #ffffff;
    color: black;
}

.icons-container{
    display: flex;
    gap: 20px;
    margin-top: 10px;
    margin-left: 25%;
}

@media(max-width:768px){
    .foot-container{
        display: inline;
    }
    .headere {
        width: 100%;
        
    }
    .left-f{
        width: 80%;
    }
    .footer-links{
        padding-left: 15px;
        gap: 10px;
        font-size: small;
    }
    .subs h1{
        font-size: medium;
        margin-left: 32px;
        width: 100%;

    }
    .etext{
        margin-left: 12%;
    }
    .h33{
        font-size: xx-small;
        margin-left: 20px;
    }
    .left-f{
        padding-left: 5px;
    }
    .right-f{
        width: 90%;
    }
}